function search(keyWord, list) {
  var keyArr = keyWord.split(' ');
  return list.filter(function (item) {
    return keyArr.some(function (info) {
      return info === item.codeType || item.description.indexOf(info) > -1 || item.tips.some(function (tip) {
        return tip === info;
      });
    });
  });
}

